* {
  padding: 0px;
  margin: 0%;
}

.header {
  width: 100%;
  height: 50px;
  z-index: 1;
  /* position: fixed; */
  background-color: black;
  display: flex;
  align-items: center;
}

.logo {
  color: whitesmoke;
  flex-grow: 1;
  padding-left: 10px;
}

.articles {
  padding-right: 50px;
}

.link {
  padding: 10px;
  text-decoration: none;
  color: whitesmoke;
}

.hidden {
  display: none;
}

.logout {
  padding: 5px;
  text-decoration: none;
  color: #fff;
  border-radius: 10%;
  background-color: #056679;
}

.logout:hover {
  background-color: silver;
  color: #056679;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
